import {
    apiHost
} from "../../config.json"

const insertResult = async (body) => {
    const host = `${apiHost}/insert_result`;

    const options = {
        'method': 'POST',
        'header': {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        'body': body,
    }

    const response = await fetch(host, options);

    if (response.ok) {
        return response.json();
    } else {
        return response.ok;
    }
}

