<script>
  import { push } from "svelte-spa-router";
  import { getTotalByStatus, getListByStatus } from "./js/List";
  import { onMount } from "svelte";
  import ListHeader from "../components/ListHeader/ListHeader.svelte";
  import { dataset_dev } from "svelte/internal";

  let status = 0;
  // parameter가 없을 때 신규 접수 페이지로 이동

  //   페이징 관련
  let currentPage = 1;
  const contentsPerPage = 10;

  let start = currentPage * contentsPerPage - contentsPerPage;
  let end = start + contentsPerPage;

  var getList = getListByStatus(status, start, end);

  const getRepresentImage = (image_url) => {
    let images = image_url.split(",");
    return images[0];
  };
  const changeStatusHandler = () => {
    getList = getListByStatus(status, start, end);
  };

  const statusString = ["신규의뢰", "견적완료", "확인완료", "답변완료"];
  const statusColor = ["bg-warning", "bg-info", "bg-primary", "bg-success"];
</script>

<ListHeader bind:status on:change={changeStatusHandler} />
<main>
  <div class="container p-2">
    <div class="row d-flex justify-content-start">
      {#await getList}
        <p>loading</p>
      {:then result}
        <div class="col-12 d-flex justify-content-end mb-2">
          <h6 class="m-0">총 {result.data.total}건</h6>
        </div>
        {#each result.data.list as data}
          <div class="col-lg-6 col-12 mb-3">
            <div class="card">
              <div class="row">
                <div class="col-4 col-md-5">
                  <div class="square bg-light rounded-5">
                    <div class="content p-2">
                      <div
                        class="d-flex justify-content-center align-items-center w-100 h-100"
                      >
                        <img
                          src={getRepresentImage(data.image_url)}
                          alt={data.request_no}
                          class="mh-100 mw-100 w-auto h-auto"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col">
                  <div class="card-body">
                    <div class="row">
                      <div class="d-flex m-0 p-0 justify-content-between">
                        <div class="">
                          <h5 class="card-title mb-0">
                            <i class="fas fa-user-circle m-1" />{data.user}
                          </h5>
                        </div>
                        <div class="">
                          <span
                            class="badge {statusColor[data.request_status]}"
                          >
                            {statusString[data.request_status]}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col mb-3">
                        <span class="badge bg-light text-dark m-0">
                          Request No. {data.request_no}
                        </span>
                      </div>
                    </div>
                    <div class="row mt-3 mt-md-5 mb-md-3">
                      <p class="card-text text-end my-auto">
                        <small>{data.reg_date}</small>
                      </p>
                    </div>
                    <button
                      class="btn btn-primary w-100"
                      on:click={() => push(`/viewer/${data.request_no}`)}
                      >자세히 보기</button
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        {/each}
      {/await}
    </div>
  </div>
</main>

<style>
  main {
    height: 100%;
    width: 100%;
    margin: 0;
    background: #eaeaea no-repeat center center fixed;
  }
  .square {
    width: 100%;
    height: 100%;
    padding-bottom: 100%;
    position: relative;
  }

  .content {
    position: absolute;
    height: 100%; /* = 100% - 2*5% padding */
    width: 100%; /* = 100% - 2*5% padding */
  }
</style>
