<script>
  export let message = "";
  export let showAlert = false;
  let closeAlert = () => {
    showAlert = false;
  };
</script>

{#if showAlert}
  <div class="alert alert-danger">
    <div class="d-flex justify-content-between align-items-center">
      <span>{message}</span>
      <button type="button" class="btn-close" on:click={closeAlert} />
    </div>
  </div>
{/if}
