<script>
  import { pop, push } from "svelte-spa-router";
  import { each } from "svelte/internal";
  import ImagePicker from "../components/ImagePicker/ImagePicker.svelte";
  import { getLivingTypes, getProductTypes, postForm } from "./js/RequestForm";
  import Alert from "../components/Alert.svelte";
  import ListHeader from "../components/ListHeader/ListHeader.svelte";

  const livingTypes = getLivingTypes();
  const productTypes = getProductTypes();
  let submitted = false;

  const makeImageUrl = () => {
    let result = "";
    for (let i = 0; i < images.length; i++) {
      if (images[i] != "" && images[i] != undefined) {
        if (result != "") result += ",";
        result += images[i];
      }
    }
    return result;
  };

  const validateValues = () => {
    image_url = makeImageUrl();
    if (user == "" || user == undefined) return false;
    else if (image_url == "" || image_url == undefined) return false;
    else if (product_type == undefined) return false;
    else if (living_type == undefined) return false;
    else if (floor_height == undefined) return false;
    else if (is_buried_pipe == undefined) return false;
    else if (uninstall_option == undefined) return false;
    else if (pipe_type == undefined) return false;
    else return true;
  };

  let showAlert = false;
  let message = "";
  let postResult;
  const handleSubmit = () => {
    if (validateValues()) {
      submitted = true;
    } else {
      showAlert = true;
      message = "입력되지 않은 값이 있습니다.";
    }
    let payload = {
      user: user,
      image_url: image_url,
      product_type: product_type,
      living_type: living_type,
      floor_height: floor_height,
      pipe_type: pipe_type,
      is_buried_pipe: is_buried_pipe,
      uninstall_option: uninstall_option,
      comment: comment,
    };
    postResult = postForm(JSON.stringify(payload));
    console.log(postResult);
  };

  // input variables
  let user;
  let image_url;
  let images = [];
  let product_type;
  let changeProductType = (event) => {
    product_type = event.currentTarget.value;
  };
  let living_type;
  let changeLivingType = (event) => {
    living_type = event.currentTarget.value;
  };
  let floor_height;
  let is_buried_pipe;
  let changeIsBuriedPipe = (event) => {
    if (event.currentTarget.value == "true") is_buried_pipe = true;
    else is_buried_pipe = false;
  };
  let uninstall_option;
  let changeUninstallOption = (event) => {
    uninstall_option = event.currentTarget.value;
  };
  let comment = "";
  let pipe_type;
</script>

<main>
  <div class="container p-3">
    <div class="row d-flex justify-content-center">
      <div class="col-12">
        <div class="card rounded-0 p-2">
          {#if !submitted}
            <div class="card-body">
              <h2 class="card-title">설치 견적 접수</h2>
              <p class="card-text">아래 양식에 맞게 작성해주세요</p>
              <hr />
              <!-- 고객명 -->
              <div class="form-outline">
                <h5 class="card-title">고객명 (카카오톡)</h5>
                <input
                  type="text"
                  bind:value={user}
                  class="form-control form-control-lg border"
                  required
                />
              </div>
              <hr />
              <!-- 사진 -->
              <div class="form-outline">
                <h5 class="card-title">사진</h5>
                <div class="row p-2">
                  <div class="col-lg-3 col-6 p-1">
                    <ImagePicker active bind:value={images[0]} />
                  </div>
                  <div class="col-lg-3 col-6 p-1">
                    <ImagePicker active bind:value={images[1]} />
                  </div>

                  <div class="col-lg-3 col-6 p-1">
                    <ImagePicker active bind:value={images[2]} />
                  </div>

                  <div class="col-lg-3 col-6 p-1">
                    <ImagePicker active bind:value={images[3]} />
                  </div>
                </div>
              </div>
              <hr />
              <!-- 상품 형태 -->
              <div class="form-outline">
                <h5 class="card-title">에어컨 형태</h5>
                <div class="btn-group m-1">
                  {#await productTypes then types}
                    {#each types as { type_no, type }}
                      <input
                        type="radio"
                        class="btn-check"
                        name="producttype"
                        id="producttype{type_no}"
                        on:change={changeProductType}
                        value={type_no}
                      />
                      <label
                        class="btn btn-light btn-lg"
                        for="producttype{type_no}">{type}</label
                      >
                    {/each}
                  {/await}
                </div>
              </div>
              <hr />
              <!-- 주거 형태 -->
              <div class="form-outline">
                <h5 class="card-title">주거 형태</h5>
                <div class="btn-group m-1">
                  {#await livingTypes then types}
                    {#each types as { type_no, type }}
                      <input
                        type="radio"
                        class="btn-check"
                        name="livingtype"
                        id="livingtype{type_no}"
                        value={type_no}
                        on:change={changeLivingType}
                      />
                      <label
                        class="btn btn-light btn-lg"
                        for="livingtype{type_no}">{type}</label
                      >
                    {/each}
                  {/await}
                </div>
              </div>
              <hr />

              <!-- 층수 -->
              <div class="form-outline">
                <h5 class="card-title">층수</h5>
                <div class="d-flex align-items-center">
                  <input
                    type="number"
                    class="form-control form-control-lg border w-25"
                    bind:value={floor_height}
                  />
                  <span class="p-1">층</span>
                </div>
              </div>
              <hr />
              <!-- 배관 타입 -->
              <div class="form-outline">
                <h5 class="card-title">배관 종류</h5>
                <div class="btn-group m-1">
                  <input
                    type="radio"
                    class="btn-check"
                    name="pipetype"
                    id="pipetype1"
                    value="true"
                    on:change={() => {
                      pipe_type = "동";
                    }}
                    required
                  />
                  <label class="btn btn-light btn-lg" for="pipetype1">동</label>
                  <input
                    type="radio"
                    class="btn-check"
                    name="pipetype"
                    id="pipetype2"
                    value="false"
                    on:change={() => {
                      pipe_type = "알루미늄";
                    }}
                    required
                  />
                  <label class="btn btn-light btn-lg" for="pipetype2"
                    >알루미늄</label
                  >
                </div>
              </div>
              <hr />
              <!-- 매립배관 여부 -->
              <div class="form-outline">
                <h5 class="card-title">매립배관 여부</h5>
                <div class="btn-group m-1">
                  <input
                    type="radio"
                    class="btn-check"
                    name="isburiedpipe"
                    id="isburiedpipe1"
                    value="true"
                    on:change={changeIsBuriedPipe}
                    required
                  />
                  <label class="btn btn-light btn-lg" for="isburiedpipe1"
                    >O</label
                  >
                  <input
                    type="radio"
                    class="btn-check"
                    name="isburiedpipe"
                    id="isburiedpipe2"
                    value="false"
                    on:change={changeIsBuriedPipe}
                    required
                  />
                  <label class="btn btn-light btn-lg" for="isburiedpipe2"
                    >X</label
                  >
                </div>
              </div>
              <hr />
              <!-- 철거 여부 -->
              <div class="form-outline">
                <h5 class="card-title">철거 여부</h5>
                <div class="btn-group m-1">
                  <input
                    type="radio"
                    class="btn-check"
                    name="uninstalloption"
                    id="uninstalloption1"
                    value="보관"
                    on:change={changeUninstallOption}
                  />
                  <label class="btn btn-light btn-lg" for="uninstalloption1">
                    철거 후 보관
                  </label>
                  <input
                    type="radio"
                    class="btn-check"
                    name="uninstalloption"
                    id="uninstalloption2"
                    value="폐기"
                    on:change={changeUninstallOption}
                  />
                  <label class="btn btn-light btn-lg" for="uninstalloption2">
                    철거 후 폐기
                  </label>
                  <input
                    type="radio"
                    class="btn-check"
                    name="uninstalloption"
                    id="uninstalloption3"
                    value="없음"
                    on:change={changeUninstallOption}
                  />
                  <label class="btn btn-light btn-lg" for="uninstalloption3">
                    기존제품 없음
                  </label>
                </div>
              </div>
              <hr />
              <!-- 요청사항 -->
              <div class="form-outline">
                <h5 class="card-title">기타 요청사항</h5>
                <textarea
                  class="form-control border"
                  bind:value={comment}
                  rows="4"
                />
              </div>

              <!-- 버튼 -->
              <div class="row mt-5">
                <div class="col-12">
                  <Alert bind:showAlert bind:message />
                </div>
                <div class="col-3">
                  <button
                    class="btn btn-lg btn-light w-100"
                    type="button"
                    on:click={() => pop()}
                  >
                    이전
                  </button>
                </div>
                <div class="col-9">
                  <button
                    class="btn btn-lg btn-success w-100"
                    type="button"
                    on:click={handleSubmit}
                  >
                    접수
                  </button>
                </div>
              </div>
            </div>
          {:else}
            <div class="card-body">
              {#await postResult}
                <p>waiting</p>
              {:then result}
                {#if result.success}
                  <div class="row mt-5 text-center">
                    <h5>
                      접수가 완료되었습니다.<br /><small
                        >접수번호 : {result.data.request_no}</small
                      >
                    </h5>
                  </div>
                  <div class="row mt-3 mb-5 d-flex justify-content-center">
                    <button class="btn btn-lg btn-light" on:click={() => pop()}
                      >돌아가기</button
                    >
                  </div>
                {:else}
                  <p>오류가 발생했습니다.</p>
                {/if}
              {/await}
            </div>
          {/if}
        </div>
      </div>
    </div>
  </div>
</main>

<style>
  main {
    height: 100%;
    width: 100%;
    margin: 0;
    background: #eaeaea no-repeat center center fixed;
  }
</style>
