<script>
  import { imgbbUploader, getDataURL } from "./imgbbUploader";

  export let value = undefined;
  export let active = false;
  export let isUploaded = false;

  let blank = "/images/blank.png";
  let imageURL = blank;
  let isUploading = false;
  let maskOpacity = 0.5;
  let input = document.createElement("input");
  input.setAttribute("type", "file");
  input.setAttribute("accept", "image/*");

  let deleteImage = () => {
    isUploaded = false;
    imageURL = blank;
    value = "";
    input.value = "";
  };

  input.onchange = async () => {
    if (input.value == "") {
      return;
    }
    imageURL = await getDataURL(input.files[0]).then((result) => {
      isUploading = true;
      maskOpacity = 0.7;
      return result;
    });

    let response = await imgbbUploader(input.files[0]).then((response) => {
      if (response["success"] == true) {
        isUploading = false;
        maskOpacity = 0;
        isUploaded = true;
        value = response["data"]["url"];
        imageURL = value;
      } else {
        imageURL = blank;
        isUploading = false;
        isUploaded = false;
        value = "";
      }
    });
  };
</script>

<div class="card border square w-100 h-100 shadow-none" id="pickerCard">
  <div class="bg-image h-100">
    <div class="d-flex justify-content-center align-items-center h-100">
      <img
        src={imageURL}
        class="align-self-center mh-100 mw-100 w-auto h-auto"
        alt="이미지"
      />
    </div>
    <div
      class="mask"
      class:d-none={isUploaded}
      style="background-color:rgba(255,255,255,{maskOpacity})"
    >
      <div class="d-flex justify-content-center align-items-center h-100">
        <button
          class="btn btn-lg btn-floating btn-light"
          disabled={!active}
          class:d-none={isUploaded || isUploading}
          on:click={input.click()}
        >
          +
        </button>
        <div
          class="spinner-border"
          class:d-none={!isUploading}
          class:d-block={isUploading}
          role="status"
        >
          <span class="visually-hidden">Uploading...</span>
        </div>
      </div>
    </div>
    <div class="mask" class:d-none={!isUploaded}>
      <div class="d-flex justify-content-end h-100 p-2">
        <button
          class="btn btn-danger btn-small btn-floating"
          on:click={deleteImage}>X</button
        >
      </div>
    </div>
  </div>
</div>

<style>
</style>
