<script>
  import { pop } from "svelte-spa-router";
  import {
    getRequest,
    insertResult,
    getResult,
    getStatus,
    updateResult,
    setStatus,
  } from "./js/Viewer";
  import ImageHeader from "../components/ImageHeader/ImageHeader.svelte";
  import Alert from "../components/Alert.svelte";
  import App from "../App.svelte";
  import { onMount } from "svelte";

  const statusString = ["신규의뢰", "견적완료", "확인완료", "답변완료"];
  const statusColor = ["bg-warning", "bg-info", "bg-primary", "bg-success"];

  var resultData = {};
  var status;

  onMount(async () => {
    let res = await getStatus(requestNo);
    status = res.data.request_status;
    if (status != 0) {
      let res = await getResult(requestNo);
      resultData = res.data;
    }
  });

  export let params = {};
  const requestNo = params.request_no;
  const getData = getRequest(requestNo);

  let showAlert = false;
  let message = "";

  var formData = {
    request_no: params.request_no,
    angle: false,
    stand: false,
    holder: false,
    pipe_extend: false,
    pipe_extend_length: undefined,
    wrinkle: false,
    wrinkle_length: undefined,
    welding: false,
    gas: false,
    drain_pump: false,
    drain_pump_length: undefined,
    hole: false,
    hole_amount: undefined,
    danger_fee: false,
    drain_kit: false,
    union_kit: false,
    power_line: false,
    air_guide: false,
  };

  const validateValues = () => {
    if (formData.pipe_extend && formData.pipe_extend_length == undefined)
      return false;
    else if (formData.wrinkle && formData.wrinkle_length == undefined)
      return false;
    else if (formData.drain_pump && formData.drain_pump_length == undefined)
      return false;
    else if (formData.hole && formData.hole_amount == undefined) return false;
    else return true;
  };

  const changeStatus = (status) => {
    var payload = { request_no: requestNo, request_status: status };
    setStatus(JSON.stringify(payload));
    submitted = true;
  };

  const validatePrice = () => {
    if (resultData.price_low <= 0 || resultData.price_low == undefined)
      return false;
    else if (resultData.price_high <= 0 || resultData.price_high == undefined)
      return false;
    else return true;
  };

  var modify = false;
  var submitted = false;
  var insertResultData;
  const handleSubmit = () => {
    if (validateValues()) {
      submitted = true;
      insertResultData = insertResult(JSON.stringify(formData)).success;
    } else {
      showAlert = true;
      message = "입력되지 않은 값이 있습니다.";
    }
  };
  var updateResultData;
  const handleUpdate = () => {
    if (validatePrice()) {
      submitted = true;
      updateResultData = updateResult(JSON.stringify(resultData)).success;
    } else {
      showAlert = true;
      message = "가격이 입력되지 않았습니다.";
    }
  };
</script>

